/**
 * http断点分片上传的插件
 * @version 1.0.5
 * @author renyuwei
 * @time   2016.11.28
 */

 import  closeImg from '../images/close.png'
 import  minImg  from '../images/min.png'
;(function ($, window, document, undefined) {
    var jsscripts = document.scripts;
    var scriptsrc = jsscripts[jsscripts.length-1].src; //获取当前js的路径
    //文件扩展名字符串
    var fileExtStr = "&ac3&ai&aiff&ani&asf&au&avi&bat&bin&bmp&bup&cab&cal&cat&cur&dat&dcr&der&dic&dll&doc&docx&dvd&dwg&dwt&fon&gif&hlp&hst&html&ico&ifo&inf&ini&java&jif&jpg&log&m4a&mmf&mmm&mov&mp2&mp2v&mp3&mp4&mpeg&msp&pdf&png&ppt&pptx&psd&ra&rar&reg&rtf&theme&Thumbs&tiff&tlb&ttf&txt&vob&wav&wma&wmv&wpl&wri&xls&xlsx&xml&xsl&zip&exe&";
    var defaults = {//默认参数
        "uploadAddress": "",                //上传文件地址
        "migrateServer":"",                 //迁移服务地址
        "retryMigrateAddress": "",          //重试迁移任务接口地址
        "clientMigrateAddress": "",         //客户端调用迁移的接口地址
        "getChunkInfoAddress": "",          //获取分片信息的接口地址
        "chunkSize": 10 * 1024 * 1024,      //分片大小
        "isRename":false,                       //是否重命名文件
        "fileSavePath":"/tmp/upload_set",       //文件目标存储路径
        "fileFormatArray":[],                   //配置插件允许上传的文件格式
        "isUploadSameFileName":false,           //默认不可上传同名文件
        "fileSizeLimit":1 * 1024 * 1024 * 1024  //默认上传大小限制
    };
    /**
     * 上传组件对象
     * @param $ele
     * @param options
     */
    var httpUploadPlugin = function ($ele, options) {
        this.pluginId = "";
        this.pluginBasePath = "";
        this.$ele = $ele;
        this.options = options;
        this.taskQueue = [];           //存储任务对象的任务队列
        this.taskIndex = -1;
        this.init();
    };
    /**
     * 上传组件的扩展方法定义
     */
    httpUploadPlugin.prototype = {
        constructor: httpUploadPlugin,
        /*初始化页面和绑定事件*/
        init: function () {
            this.getPluginBasePath();
        },
        /*自动生成id*/
        guidGenerator: function () {
            var S4 = function () {
                return (((1 + Math.random()) * 0x10000) | 0).toString(16).substring(1);
            };
            return (S4() + S4() + "-" + S4() + "-" + S4() + "-" + S4() + "-" + S4() + S4() + S4());
        },
        /*获取插件的base地址*/
        getPluginBasePath:function() {
            var jspath = scriptsrc.substring(0,scriptsrc.lastIndexOf("/"));
            this.pluginBasePath = jspath.substring(0,jspath.lastIndexOf("/") + 1);
            return this.pluginBasePath;
        },
        /*切分字符串，支持中英文*/
        splitStr: function (str, n) {
            var r = /[^\x00-\xff]/g;
            if (str.replace(r, "mm").length <= n) {
                return str;
            }
            var m = Math.floor(n / 2);
            for (var i = m; i < str.length; i++) {
                if (str.substr(0, i).replace(r, "mm").length >= n) {
                    return str.substr(0, i) + "...";
                }
            }
            return str;
        },
        //字节转换成其它类型的单位
        bytesToSize: function (bytes) {
            if (bytes === 0) return '0 B';
            var k = 1024;
            var sizes = ['B', 'KB', 'MB', 'GB', 'TB', 'PB', 'EB', 'ZB', 'YB'];
            var i = Math.floor(Math.log(bytes) / Math.log(k));
            return (bytes / Math.pow(k, i)).toFixed(2) + ' ' + sizes[i];
        },
        //文件格式过滤
        fileFormatFilter: function (uploadFileArray) {
            var fileFormatArray = this.options.fileFormatArray;
            if (fileFormatArray == null || fileFormatArray.length == 0) {
                return true;
            }
            for (var i = 0; i < uploadFileArray.length; i++) {
                var fileName = uploadFileArray[i].name;
                var extFileName = fileName.substring(fileName.lastIndexOf(".") + 1);
                extFileName = extFileName.toLowerCase();
                for (var j = 0; j < fileFormatArray.length; j++) {
                    var fileExt = fileFormatArray[j];
                    if (fileExt == extFileName) {
                        break;
                    }
                }
                if (j == fileFormatArray.length) {
                    return false;
                }
            }
            return true;
        },
        //根据配置判断是否存在同名文件上传,选择的文件和上传队列文件进行比较
        isSameNameFile: function (uploadFileArray) {
            for (var i = 0; i < uploadFileArray.length; i++) {
                var fileName = uploadFileArray[i].name;
                var taskQueue = this.taskQueue;
                if (taskQueue == null) {
                    break;
                }
                for (var j = 0; j < taskQueue.length; j++) {
                    if (taskQueue[j] != null) {
                        var name = taskQueue[j].fileName;
                        if (fileName == name) {
                            return true;
                        }
                    }
                }
            }
            return false;
        },
        //根据配置判断上传文件是否超出大小限制
        isExceedLimitSize: function (uploadFileArray) {
            var limitSize = this.options.fileSizeLimit;
            for (var i = 0; i < uploadFileArray.length; i++) {
                var size = uploadFileArray[i].size;
                if (size > limitSize) {
                    return true;
                }
            }
            return false;
        },
        /*渲染初始页面*/
        renderHtml: function (actionType) {
            this.pluginId = this.guidGenerator(); //生成插件Id，标识插件的唯一性
            var options = this.options;
            var html = '';
            html = '<div id="httpupload-plugin-'+this.pluginId+'" class="dy-httpupload-style1"><div class="dy-httpupload-style1-top"><div class="dy-httpupload-left"><span id="httpupload-plugin-uploadcontip-'+this.pluginId+'">上传完成</span>（<span id="httpupload-plugin-uploadfinishnum-'+this.pluginId+'">0</span>/<span id="httpupload-plugin-uploadallnum-'+this.pluginId+'">0</span>）</div>'+
                '<div class="dy-httpupload-right"><a id="httpupload-plugin-minmaximize-'+this.pluginId+'"><img src="'+ minImg+'" style="margin-right:10px;" /></a>'+
                '<a id="httpupload-plugin-close-'+this.pluginId+'" ><img src=" ' + closeImg+'"/></a></div></div>'+
                '<div id="httpupload-plugin-content-'+this.pluginId+'" class="dy-httpupload-style1-content"><div class="dy-httpupload-style2"><div class="dy-httpupload-style2-2">文件名</div>'+
                '<div class="dy-httpupload-style2-3">大小</div><div class="dy-httpupload-style2-5">状态</div>'+
                '<div class="dy-httpupload-style2-6">操作</div></div>'+
                '<div id="httpupload-plugin-container-'+this.pluginId+'" style="position:relative; height:300px; overflow-y:auto; overflow-x:hidden"></div>' ;
            if(actionType == 0){
            	html += '<input id="httpupload-plugin-file-input-'+this.pluginId+'" type="file" accept="image/*" capture="camera" style="display:none;"  multiple="multiple"/>';
            }else if(actionType == 1){
            	html += '<input id="httpupload-plugin-file-input-'+this.pluginId+'" type="file" accept="image/*" style="display:none;"  multiple="multiple"/>';
            }else{
            	html += '<input id="httpupload-plugin-file-input-'+this.pluginId+'" type="file" style="display:none;"  multiple="multiple"/>';
            }
                html += '</div></div>';
            this.$ele.append(html);
        },
        /*
         *调用回调函数，将传入的参数和文件详情信息
         *返回
         */
        callFunc:function(taskIndex) {
            var uploadTask = this.taskQueue[taskIndex];
            var params = uploadTask.params;
            var callFunc = uploadTask.callFunc;
            var returnJson = {};
            returnJson.fileSessionId = uploadTask.fileSessionId;
            returnJson.fileName = uploadTask.fileName;
            returnJson.migrateFileName = uploadTask.migrateFileName;
            returnJson.offset = uploadTask.offset;
            returnJson.stop = uploadTask.stop;
            returnJson.finishProgress = uploadTask.finishProgress;
            returnJson.responseStatus = uploadTask.responseStatus;
            returnJson.fileSize = uploadTask.uploadFile.size;
            returnJson.params = params;
            //传入参数不为空，则进行回调
            if(params != null && params != "") {
                callFunc(JSON.stringify(returnJson));
            }
        },
        //上传完成回调函数
        complete: function (xhr,fileSize,fileSessionIdPara,taskIndex,that) {
            that.taskQueue[taskIndex].stop = true;  //置任务为停止
            if (!that.isTaskUploading()) {
                $('#httpupload-plugin-uploadcontip-'+that.pluginId).html("上传完成");
            }
            if (xhr.status == 200) {
                //上传成功
                that.taskQueue[taskIndex].taskStatus = 2;
                that.getAllAndFinishNum(false);
                that.renderTipInfo(0,fileSessionIdPara,taskIndex);
                var returnJson = JSON.parse(xhr.responseText);
                that.taskQueue[taskIndex].migrateFileName = returnJson.migrateFileName;
                that.taskQueue[taskIndex].fileName = returnJson.originFileName;
                that.callFunc(taskIndex);
                that.uploadNext(that);
            } else {
                //上传失败
                that.taskQueue[taskIndex].taskStatus = 1;
                that.renderTipInfo(1,fileSessionIdPara,taskIndex);
                that.uploadNext(that);//上传失败也同时触发上传下一个
                //为重试图标添加事件
                $("#httpupload-plugin-failturetip-" + fileSessionIdPara+"-"+taskIndex).off("click");
                $("#httpupload-plugin-failturetip-" + fileSessionIdPara+"-"+taskIndex).on("click",function(){
                    $('#httpupload-plugin-uploadcontip-'+that.pluginId).html("正在上传");
                    if (that.isTaskUploading()) {
                        that.taskQueue[taskIndex].taskStatus = 0; //点击重试，任务状态置为0，；排队中
                        that.renderTipInfo(2,fileSessionIdPara,taskIndex);
                        return;
                    }
                    var data = that.getChunkInfo(taskIndex,that);
                    if (data == null || data=="" || typeof data == "undefined") {
                        alert("重试失败,请稍后重试!");
                        $('#httpupload-plugin-uploadcontip-'+that.pluginId).html("上传完成");
                        return;
                    }
                    var status = data.uploadStatus;
                    if (status == 0) {
                        //分片没有上传完成，重新续传
                        that.doUplaod(taskIndex,that.taskQueue[taskIndex].uploadFile, that.taskQueue[taskIndex].fileSessionId, that.progress, that.complete);//重启该任务上传
                    } else if (status == 3 || status == 4) {
                        if (that.taskQueue[taskIndex].uploadFile.size == 0) {
                            alert("请重新选择文件上传,该文件不存在！");
                            $('#httpupload-plugin-uploadcontip-'+that.pluginId).html("上传完成");
                        } else {
                            that.doUplaod(taskIndex,that.taskQueue[taskIndex].uploadFile, that.taskQueue[taskIndex].fileSessionId, that.progress, that.complete);//重启该任务上传
                        }
                    } else if (status == 1) {
                        //上传完成，但没有迁移任务，客户端调用迁移
                        that.clientCallMigrate(taskIndex,that);
                    } else if (status == 2) {
                        //迁移任务添加成功，但上传文件迁移失败
                        that.retryMigrate(taskIndex,that);
                    } else {
                        alert(data.description);
                        $('#httpupload-plugin-uploadcontip-'+that.pluginId).html("上传完成");
                    }
                });
            }
        },
        //迁移失败，重试迁移任务
        retryMigrate:function(taskIndex,that) {
            var fileSessionId = that.taskQueue[taskIndex].fileSessionId;
            var migrateAddress = that.options.retryMigrateAddress;
            var array = [];
            array.push(fileSessionId);
            var obj = {"fileSessionIdArray":array};
            var objstr = JSON.stringify(obj);
            $.ajax({
                    type: 'POST',
                    //请求类型 post
                    dataType: "json",
                    url: migrateAddress,
                    //请求地址
                    data: objstr,
                    //请求数据
                    success: function(data) {
                        var returnResult = data[0];
                        if (returnResult.success) {
                            $('#httpupload-plugin-uploadcontip-'+that.pluginId).html("上传完成");
                            that.taskQueue[taskIndex].responseStatus = 200;
                            that.taskQueue[taskIndex].taskStatus = 2;
                            that.renderTipInfo(0,fileSessionId,taskIndex);
                            that.getAllAndFinishNum(false);
                            that.callFunc(taskIndex);
                        }
                    }
                } //请求成功时操作
            );
        },
        //客户端调用迁移服务
        clientCallMigrate:function(taskIndex,that) {
            var fileSessionId = that.taskQueue[taskIndex].fileSessionId;
            var uploadFile = that.taskQueue[taskIndex].uploadFile;
            var clientMigrateAddress = that.options.clientMigrateAddress;
            $.ajax({
                    type: 'POST',
                    //请求类型 post
                    dataType: "json",
                    url: clientMigrateAddress,
                    //请求地址
                    data: {
                        "fileSessionId":fileSessionId,
                        "fileName":uploadFile.name,
                        "filePath":fileSessionId,
                        "fileSavePath":that.options.fileSavePath,
                        "isRename":that.options.isRename
                    },
                    //请求数据
                    success: function(data) {
                        if (data.migrateStatus) {
                            $('#httpupload-plugin-uploadcontip-'+that.pluginId).html("上传完成");
                            that.taskQueue[taskIndex].responseStatus = 200;
                            that.taskQueue[taskIndex].taskStatus = 2;
                            that.renderTipInfo(0,fileSessionId,taskIndex);
                            that.getAllAndFinishNum(false);
                            that.callFunc(taskIndex);
                        }
                    }
                } //请求成功时操作
            );
        },
        //客户端调用获取分片信息接口
        getChunkInfo:function(taskIndex,that) {
            var result = "";
            var fileSessionId = that.taskQueue[taskIndex].fileSessionId;
            var getChunkInfoAddress = that.options.getChunkInfoAddress;
            $.ajax({
                    type: 'POST',
                    //请求类型 post
                    dataType: "json",
                    async:false,
                    url: getChunkInfoAddress,
                    //请求地址
                    data: JSON.stringify({
                        "fileSessionId":fileSessionId
                    }),
                    //请求数据
                    success: function(data) {
                        result = data;
                    }
                } //请求成功时操作
            );
            return result;
        },
        //上传进度回调函数
        progress: function (progressValue,fileSessionIdPara,taskIndex) {
            console.log("上传百分比： "+Math.floor(progressValue * 100) + "%");
            $("#httpupload-plugin-uploadpercent-" + fileSessionIdPara+"-"+taskIndex).html(Math.floor(progressValue * 100) + "%");
            $("#httpupload-plugin-uploadcontrol-" + fileSessionIdPara+"-"+taskIndex).css("width", Math.floor(progressValue * 100) + "%");//进度条变化
        },
        /**
         * 当点击上传按钮时，主动调用该
         * 方法实现上传文件的选择
         * @param callFunc
         * @param params
         * @param existFileName
         * @param actionType 0:拍照,1:图片,2:其他文件
         */
        startSelectFile:function(params,callFunc,existFileName,actionType) {
            var pluginId = $("#httpupload-plugin-"+this.pluginId);
            if (pluginId.length == 0) {
                this.renderHtml(actionType);                  //渲染插件初始页面
            }
            this.bindEvent(params,callFunc,existFileName);    //绑定页面事件
            $("#httpupload-plugin-file-input-"+this.pluginId).click();

        },

        /**
         * 为初始化页面绑定事件，添加
         * 上传进度回调函数，上传完成
         * 回调函数。
         */
        bindEvent: function (params,callFunc,existFileName) {
            var that = this;
            //去除原先绑定的事件
            $("#httpupload-plugin-minmaximize-"+this.pluginId).off("click");
            $("#httpupload-plugin-file-input-"+this.pluginId).off("change");
            $("#httpupload-plugin-close-"+this.pluginId).off("click");
            //绑定页面最大最小事件
            $("#httpupload-plugin-minmaximize-"+this.pluginId).on("click",function(){
                var image = $(this).find("img").eq(0);
                if (image.attr("src").indexOf("min.png") != -1) {
                    var maximag = image.attr("src").replace("min.png","max.png");
                    $("#httpupload-plugin-content-"+that.pluginId).hide();
                    $("#httpupload-plugin-"+that.pluginId).css("height","46px");
                    image.attr("src",maximag);
                } else {
                    var minimag = image.attr("src").replace("max.png","min.png");
                    $("#httpupload-plugin-content-"+that.pluginId).show();
                    $("#httpupload-plugin-"+that.pluginId).css("height","400px");
                    image.attr("src",minimag);
                }
            });
            //绑定页面关闭事件
            $("#httpupload-plugin-close-" + this.pluginId).on("click", function () {
                if (that.isTaskUploading()) {
                    var result = confirm("有正在上传的任务，你确定要删除任务？");
                    if (result) {
                        $("#httpupload-plugin-" + that.pluginId).remove();  //去除页面展示
                        that.taskQueue.length = 0;        //清空数组
                        that.taskIndex = -1;
                    }
                } else {
                    $("#httpupload-plugin-" + that.pluginId).remove();  //去除页面展示
                    that.taskQueue.length = 0;        //清空数组
                    that.taskIndex = -1;
                }
            });
            //文件选择完成后，触发上传功能，添加上传进度和成功上传回调事件
            $("#httpupload-plugin-file-input-"+this.pluginId).on("change", function () {
                var uploadFileArray = $('#httpupload-plugin-file-input-'+that.pluginId).get(0).files;
                var result = that.fileFormatFilter(uploadFileArray);  //判断选择的文件类型是否允许上传
                if (!result) {
                    alert("你选择的文件类型受到配置限制，请查看！");
                    return;
                }
                var isSameName = that.options.isUploadSameFileName;
                var result = that.isSameNameFile(uploadFileArray);    //判断选择的文件中是否有和上传任务队列文件同名的文件
                if (result && !isSameName) {
                    alert("不能上传同样名称的文件！");
                    return;
                }
                for(var m in existFileName){
                	for (var i = 0; i < uploadFileArray.length; i++) {
                        var fileName = uploadFileArray[i].name;
                        if(existFileName[m] == fileName){
                    		alert("不能上传同样名称的文件！");
                    		return;
                    	}
                    }
                }
                var sizeResult = that.isExceedLimitSize(uploadFileArray); //判断选择的文件中，是否有文件大小超出插件最大限制的文件
                if (sizeResult) {
                    alert("你选择的文件中，有超出文件大小限制的文件，请查看！");
                    return;
                }
                $('#httpupload-plugin-'+that.pluginId).show();//选择文件后展示插件渲染页面
                for (var i = 0;i < uploadFileArray.length;i++) {
                    var fileSessionId = that.guidGenerator();//生成fileSesionId
                    that.addUpload(uploadFileArray[i], fileSessionId, that.progress, that.complete,params,callFunc);
                }
                $('#httpupload-plugin-file-input-'+that.pluginId).val("");//置文件输入框为空,下次可以选择同样文件上传
            });
        },
        /*从头开始遍历，如果有排队中的任务，则触发上传*/
        uploadNext: function (that) {
            if (that.taskQueue == null) return;
            for (var i = 0; i < that.taskQueue.length; i++) {
                if (that.taskQueue[i] != null) {
                    if (that.taskQueue[i].taskStatus == 0) {
                        that.taskQueue[i].stop = false;
                        that.doUplaod(i,that.taskQueue[i].uploadFile, that.taskQueue[i].fileSessionId, that.progress, that.complete);//添加上传功能
                        break;
                    }
                }
            }
        },
        /**
         * 根据传入的参数返回队列总数目
         * 或者完成上传的数据提示
         * true  上传队列总数目
         * false 上传成功完成的总数目
         * @param flag
         */
        getAllAndFinishNum: function (flag) {
            var allnum = 0;
            var finishnum = 0;
            for (var i = 0; i < this.taskQueue.length; i++) {
                if (this.taskQueue[i] != null && !this.taskQueue[i].isDelete) {
                    allnum++;        //上传队列的总数目
                    if (this.taskQueue[i].taskStatus == 2) {
                        finishnum++; //上传成功的总数目
                    }
                }
            }
            if (flag) {
                //更新队列总数目
                $('#httpupload-plugin-uploadallnum-' + this.pluginId).html(allnum);
            } else {
                //更新已完成队列数目
                $('#httpupload-plugin-uploadfinishnum-' + this.pluginId).html(finishnum);
            }
        },
        /**
         * 添加上传任务
         */
        addUpload: function (uploadFile, fileSessionId, progress, complete,params,callFunc) {
            this.taskIndex++;
            this.addUploadHtml(fileSessionId, uploadFile,this.taskIndex);         //渲染新添加的任务
            this.bindUploadHtmlEvent(fileSessionId,this.taskIndex);                  //为渲染的任务页面绑定事件
            var fileName = uploadFile.name;
            //添加任务队列
            this.taskQueue[this.taskIndex] = {
                "taskStatus": 0,                        //0表示排队中，1上传失败，2上传成功
                "stop":true,                           //任务是否暂停
                "isDelete":false,                      //任务是否删除
                "finishProgress":0,                    //上传进度0-100，100表示完成
                "uploadFile":uploadFile,               //上传文件对象
                "offset":0,                            //上传偏移值
                "fileName":fileName,                   //源文件名
                "migrateFileName":"",                  //迁移后的文件名，上传完成后返回
                "fileSessionId":fileSessionId,         //上传的fileSessionId
                "responseStatus":"",                   //上传完成后状态码
                "params":params,                       //传入的参数
                "callFunc":callFunc                    //回调方法
            };
            this.getAllAndFinishNum(true);             //更新总数目信息展示
            //判断任务队列中是否有正在上传任务
            if (!this.isTaskUploading()) {
                this.doUplaod(this.taskIndex,uploadFile, fileSessionId, progress, complete);//添加上传功能
            }
        },
        //判断是否有任务正在上传中
        isTaskUploading: function () {
            for (var i= 0;i<this.taskQueue.length;i++ ) {
                if(this.taskQueue[i] != null) {
                    var taskStatus = this.taskQueue[i].stop;
                    if (!taskStatus) {
                        return true;
                    }
                }
            }
            return false;
        },
        //根据文件类型获取文件图标
        getIconNameByFileType:function(fileName) {
            if (fileName != null) {
                var extFileName = fileName.substring(fileName.lastIndexOf(".") + 1);
                extFileName = extFileName.toLowerCase();
                if (fileExtStr.indexOf("&"+extFileName+"&") != -1) {
                    return extFileName+".png";
                } else {
                    return "unk.png";
                }
            } else {
                return "unk.png";
            }
        },
        //渲染新添加的上传任务页面
        addUploadHtml: function (fileSessionId, uploadFile,taskIndex) {
            var html = '';
            var filetypeIconPath = this.pluginBasePath+"images/"+this.getIconNameByFileType(uploadFile.name);
            var filesizevalue = this.bytesToSize(uploadFile.size);
            var fileName = this.splitStr(uploadFile.name,45);
            html = '<div id="httpupload-plugin-uploadcontrol-' + fileSessionId + '-'+taskIndex +'" class="dy-httpupload-jindu"><div class="dy-httpupload-style2">'+
                '<div class="dy-httpupload-style2-2"><div style="width:24px; height:24px;margin-right:10px; position:absolute;">'+
                '<img src="'+filetypeIconPath+'" height="24px"/></div><span class="dy-httpupload-style2-2-3" id="httpupload-plugin-filename-' + fileSessionId + '-'+taskIndex +'">' + fileName + '</span></div>'+
                '<div class="dy-httpupload-style2-3">'+filesizevalue+'</div><div id="httpupload-plugin-uploadpercent-' + fileSessionId + '-'+taskIndex +'" class="dy-httpupload-style2-5">排队中</div><div class="dy-httpupload-style2-6"><a id="httpupload-plugin-delete-' + fileSessionId + '-'+taskIndex +'">'+
                '<img src="'+ this.pluginBasePath+'/images/delete.png" style="" width="16px" /></a></div></div></div>';
            $("#httpupload-plugin-container-"+this.pluginId).append(html);
        },
        //绑定添加的上传任务页面事件
        bindUploadHtmlEvent: function (fileSessionId,taskindex) {
            var that = this;
            //为删除上传任务按钮绑定事件
            $("#httpupload-plugin-delete-" + fileSessionId+"-"+taskindex).on("click", function () {
                $("#httpupload-plugin-uploadcontrol-" + fileSessionId+"-"+taskindex).remove();
                that.deleteUpload(taskindex);  //删除队列任务
                that.getAllAndFinishNum(true); //更新成功上传数目信息提示
            });
        },
        /*
         * 渲染上传结果提示信息，包括文字提示和图标展示
         * true  上传成功
         * false 上传失败
         */
        renderTipInfo:function(tiptag,fileSessionId,taskIndex) {
            if (tiptag == 0) {
                //上传成功提示
                $("#httpupload-plugin-uploadpercent-" + fileSessionId+"-"+taskIndex).html('<span style="color:#12961C">成功</span>');
                //去除删除图标
                $("#httpupload-plugin-delete-" + fileSessionId+"-"+taskIndex).hide();
            } else if (tiptag == 1){
                //上传失败提示
                var failtureTip = '<a>失败</a><a id="httpupload-plugin-failturetip-' + fileSessionId + '-'+taskIndex +'" style="color:#2098F0">重试</a>';
                $("#httpupload-plugin-uploadpercent-" + fileSessionId+"-"+taskIndex).html(failtureTip);
            } else if(tiptag == 2) {
                $("#httpupload-plugin-uploadpercent-" + fileSessionId+"-"+taskIndex).html("排队中");
            }
        },
        //开始上传文件
        doUplaod: function (taskIndex,file, fileSessionId, progress, complete) {
            $('#httpupload-plugin-uploadcontip-'+this.pluginId).html("正在上传");
            this.taskQueue[taskIndex].stop = false;
            var that = this;
            var url = that.options.uploadAddress;
            var aborting = false, TO = null;            //定义变量
            var xhr = null;
            var offset = this.taskQueue[taskIndex].offset;
            //上传分片
            var uploadNextChunk = function () {
                var TO = null;
                var chunkStart =Math.min(offset, file.size-1) , chunkEnd = Math.min(offset + that.options.chunkSize, file.size) - 1;
                var currentBlob = (file.slice || file.mozSlice || file.webkitSlice).call(file, chunkStart, chunkEnd + 1);
                if (!(currentBlob && currentBlob.size > 0)) {
                    if (chunkEnd + 1 - chunkStart > 0) {
                        alert("文件不存在，请重新上传！"); // Sometimes the browser reports an empty chunk when it shouldn't, could retry here
                        return;
                    }
                }
                xhr = new XMLHttpRequest();
                if (chunkEnd === file.size - 1) {
                    // Add extra URL params on the last chunk
                    // Important: URL parameters passing this doesn't work currently, pass parameters via some custom "X-" header instead
                    var redirectParam = JSON.stringify({"fileSavePath":that.options.fileSavePath,"fileSessionId":fileSessionId,"isRename":that.options.isRename});
                    xhr.open('POST', url + (url.indexOf('?') > -1 ? '&' : '?') + "redirectParam="+redirectParam, true);
                    // xhr.open('POST', url, true);
                } else {
                    xhr.open('POST', url, true);
                }
                // 进度监控
                xhr.upload.addEventListener('progress', function (e) {
                    if (that.taskQueue[taskIndex] == null) {
                        return;
                    }
                    if (aborting) {
                        return;
                    }
                    that.taskQueue[taskIndex].finishProgress = Math.floor(((chunkStart + e.loaded) / file.size)*100);
                    progress((chunkStart + e.loaded) / file.size,that.taskQueue[taskIndex].fileSessionId,taskIndex);
                });
                xhr.addEventListener('load', function () {
                    if (that.taskQueue[taskIndex] == null) {
                        return;
                    }
                    if (aborting) {
                        return;
                    }
                    that.taskQueue[taskIndex].responseStatus = xhr.status;
                    if (xhr.readyState >= 4) {
                        that.taskQueue[taskIndex].offset = chunkEnd + 1;
                        if (xhr.status === 201) {
                            //分片上传完成
                            offset = chunkEnd + 1;
                            TO = setTimeout(uploadNextChunk, 1); // attempt to avoid xhrs sticking around longer than needed
                        } else {
                            //上传完成
                            console.log("ajax请求响应上传完成");
                            complete(xhr,file.size,that.taskQueue[taskIndex].fileSessionId,taskIndex,that);
                        }
                    } else {
                        console.log("ajax请求响应xhr.readyState小于4");
                        complete(xhr,file.size,that.taskQueue[taskIndex].fileSessionId,taskIndex,that);
                    }
                });
                xhr.addEventListener('error', function () {
                    that.taskQueue[taskIndex].responseStatus = xhr.status;
                    console.log("ajax请求响应出现错误");
                    complete(xhr,file.size,that.taskQueue[taskIndex].fileSessionId,taskIndex,that);
                });
                //按照nginx_upload_module的http协议进行封装Post头部
                xhr.setRequestHeader('Content-Type', 'application/octet-stream');
                xhr.setRequestHeader('Content-Disposition', 'attachment; filename="' + encodeURIComponent(file.name) + '"');
                xhr.setRequestHeader('X-Content-Range', 'bytes ' + chunkStart + '-' + chunkEnd + '/' + file.size);
                xhr.setRequestHeader('X-Session-ID', fileSessionId);
                xhr.send(currentBlob);//发送请求，上传数据
            };
            TO = setTimeout(uploadNextChunk, 1);
            return {
                abort: function () {
                    aborting = true;
                    if (TO !== null) {
                        clearTimeout(TO);
                        TO = null;
                    }
                    try {
                        xhr.abort();
                    } catch (err) {
                    }
                },
                pause: function () {
                    this.abort();
                    aborting = false;
                },
                resume: function () {
                    uploadNextChunk();
                }
            };
        },
        //停止上传的任务，置指针为空
        deleteUpload: function (taskindex) {
            if (this.taskQueue[taskindex] != null && !this.taskQueue[taskindex].stop) {
                //停止并删除正在上传的任务
                this.taskQueue[taskindex].isDelete = true;
                this.taskQueue[taskindex].stop = true;
                this.taskQueue[taskindex] = null;
                if (!this.isTaskUploading()) {
                    $('#httpupload-plugin-uploadcontip-'+this.pluginId).html("上传完成");
                }
                this.uploadNext(this);                          //接着下条任务上传
            } else if (this.taskQueue[taskindex] != null) {
                if(this.taskQueue[taskindex].stop) {
                    //如果是删除任务，则任务队列指针置为null
                    this.taskQueue[taskindex] = null;
                }
            }
        },
        //是否完成所有上传任务
        isFinishedAllTasks: function() {
            var finishStatus = true;
            var returnJsonObj = {"finishStatus": true,"description": "全部上传完成！"};
            if (this.taskQueue.length > 0) {
                for (var i = 0; i < this.taskQueue.length; i++) {
                    var uploadTask = this.taskQueue[i];
                    if (uploadTask != null) {
                        if (uploadTask.taskStatus != 2) {
                            returnJsonObj.finishStatus = false;
                            returnJsonObj.description = "没有全部完成！";
                        }
                    }
                }
            } else {
                returnJsonObj.description = "无上传任务！";
            }
            return returnJsonObj;
        },
        //获取上传任务详情
        getUploadTasksDetail: function() {
            var returnJson = {};
            var taskArray = [];
            if (this.taskQueue.length > 0) {
                for (var i = 0; i < this.taskQueue.length; i++) {
                    var uploadTask = this.taskQueue[i];
                    if (uploadTask != null) {
                        var uploadTaskJson = {};
                        uploadTaskJson.fileSessionId = uploadTask.fileSessionId;
                        uploadTaskJson.fileName = uploadTask.fileName;
                        uploadTaskJson.migrateFileName = uploadTask.migrateFileName;
                        uploadTaskJson.offset = uploadTask.offset;
                        uploadTaskJson.stop = uploadTask.stop;
                        uploadTaskJson.finishProgress = uploadTask.finishProgress;
                        uploadTaskJson.responseStatus = uploadTask.responseStatus;
                        uploadTaskJson.fileSize = uploadTask.uploadFile.size;
                        taskArray.push(uploadTaskJson);
                    }
                }
            } else {
                returnJson.description = "没有上传任务！";
            }
            returnJson.uploadTasksDetail = taskArray;
            returnJson.description = "返回成功！";
            return returnJson;
        },
        //清除队列资源,置上传队列对象为空
        clearPlugin:function() {
            for (var i = 0; i < this.taskQueue.length; i++) {
                if(this.taskQueue[i] != null) {
                    this.taskQueue[i] = null;
                }
            }
        }
    };
    //为jquery扩展方法，返回插件对象
    $.extend({
        httpUploadPlugin:function(options){
            if (!this.paramsFormatCheck(options)) return;     //验证必填参数
            options = $.extend(defaults, options || {});
            return new httpUploadPlugin($("#shishixingbuxing"), options);
        },
        /**
         * 验证必填参数的格式
         * 并设置其它参数的值
         * @param options
         * @returns {boolean}
         */
        paramsFormatCheck:function (options) {
            var regexStr = "[a-zA-z]+://[^\s]*";            //验证url的正则表达式
            var regExp = new RegExp(regexStr);
            if (options.uploadAddress != null && options.uploadAddress != "") {
                var result = regExp.test(options.uploadAddress);
                if (result) {
                    //正则匹配成功
                    if (options.uploadAddress.indexOf("?remoteRootPath=") != -1) {
                        var uploadAddress = options.uploadAddress;
                        options.uploadAddress = uploadAddress.substring(0, uploadAddress.indexOf("?")) + "/";
                        if (uploadAddress.indexOf("&") != -1) {
                            var fileSavePathParam = uploadAddress.substring(uploadAddress.indexOf("?") + 1, uploadAddress.indexOf("&"));
                            options.fileSavePath = fileSavePathParam.split("=")[1];
                        } else {
                            var fileSavePathParam = uploadAddress.substring(uploadAddress.indexOf("?") + 1);
                            options.fileSavePath = fileSavePathParam.split("=")[1];
                        }
                    } else {
                        alert("没有输入临时存储目录参数！");
                        return false;
                    }
                } else {
                    alert("你输入的上传地址格式有误!");
                    return false;
                }
            } else {
                alert("上传地址不能为空！");
                return false;
            }
            //开始验证迁移服务地址,并拼接其它三个接口地址
            if (options.migrateServer != null && options.migrateServer != "") {
                var result = regExp.test(options.migrateServer);
                if (result) {
                    //正则匹配成功
                    options.retryMigrateAddress = options.migrateServer + "rs/retryMigrate";
                    options.getChunkInfoAddress = options.migrateServer + "rs/getUploadChunkInfo";
                    options.clientMigrateAddress = options.migrateServer + "MigrateController/clientMigrateServer.do";
                } else {
                    alert("你输入的迁移服务地址格式有误!");
                    return false;
                }
            } else {
                alert("迁移服务地址不能为空！");
                return false;
            }
            return true;
        }
    });
})(jQuery, window, document);



